


























































import Vue from 'vue'
import Edit from './components/edit.vue'
import { getRoles } from '@/services/role'
import dayjs from 'dayjs'

export default Vue.extend({
  components: {
    Edit
  },
  data() {
    return {
      dialogVisible: false,
      roles: [],
      loading: false,
      roleId: null,
      isEdit: false,
      currentRole: {}
    }
  },
  filters: {
    formatTime(timestr: string) {
      return dayjs(timestr).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  created() {
    this.loadRoles()
  },
  methods: {
    handleClose() {
      this.dialogVisible = false
      this.currentRole = {}
    },
    onSuccess() {
      this.loadRoles()
      this.handleClose()
    },
    addRole() {
      this.isEdit = false
      this.dialogVisible = true
    },
    handleEdit(row: any) {
      this.roleId = row.id
      this.isEdit = true
      this.currentRole = row
      this.dialogVisible = true
    },
    async loadRoles() {
      this.loading = true
      const { data } = await getRoles()
      this.loading = false
      this.roles = data.data
    }
  }
})

